package io.github.mee1080.umasim.data

import kotlin.math.max
import kotlin.math.min
import kotlin.random.Random

data class SupportCardSpecialUnique(
    val type: Int,
    val value0: Int,
    val value1: Int,
    val value2: Int,
    val value3: Int,
    val value4: Int,
) {
    val description
        get() = when (type) {
            101 -> "${value0}${supportEffectName[value1]}$value2" + if (value3 > 0) "${supportEffectName[value3]}$value4" else ""
            102 -> "${value0}$value1"
            103 -> "${value0}$value1"
            104 -> "${value0}1${value1}"
            105 -> "+10 or /+25"
            106 -> "${supportEffectName[value1]}${value2}${value0}"
            107 -> "30${supportEffectName[value0]}151-0.15${type},${value0},${value1},${value2},${value3},${value4}"
            108 -> "100${supportEffectName[value0]}${value3}1+0.$value2${value4}"
            109 -> "${supportEffectName[value0]}${value1}"
            110 -> "${supportEffectName[value0]}${value1}"
            111 -> "${supportEffectName[value0]}Lv$value1"
            112 -> "$value0%0%"
            113 -> "${supportEffectName[value0]}$value1"
            114 -> "${supportEffectName[value0]}${value2}-(100-)/${value1}+20"
            115 -> "${supportEffectName[value0]}+${value1}"
            116 -> "${supportEffectName[value1]}${skillEffectName[value0] ?: "($value0)"}${value2}${value3}"
            117 -> "${supportEffectName[value0]}Lv1${value2}"
            118 -> "${value1}2"
            else -> if (supportEffectName.containsKey(type)) {
                "${supportEffectName[type]}$value0"
            } else "${type},${value0},${value1},${value2},${value3},${value4}"
        }

    val targetRelation = when (type) {
        101, 102 -> value0
        118 -> value1
        else -> null
    }

    val needCheckFriendCount = type == 106

    fun getMotivation(
        relation: Int,
        friendTraining: Boolean,
    ): Int {
        return if (type == 101 && relation >= value0 && value1 == 2) {
            value2
        } else if (type == 113 && friendTraining && value0 == 2) {
            value1
        } else 0
    }

    fun getBaseBonus(
        statusType: StatusType,
        relation: Int,
        healSkillCount: Int,
    ): Int {
        return if (type == 101 && relation >= value0) {
            listOf(value1 to value2, value3 to value4).sumOf {
                if ((it.first == 41 && statusType != StatusType.SKILL) || statusType == bonusStatus(it.first)) it.second else 0
            }
        } else if (type == 116) {
            val skillCount = when (value0) {
                3 -> healSkillCount
                // TODO
                else -> 0
            }
            if (statusType == bonusStatus(value1)) {
                value2 * min(skillCount, value3)
            } else 0
        } else 0
    }

    private fun bonusStatus(value: Int) = when (value) {
        3 -> StatusType.SPEED
        4 -> StatusType.STAMINA
        5 -> StatusType.POWER
        6 -> StatusType.GUTS
        7 -> StatusType.WISDOM
        30 -> StatusType.SKILL
        else -> null
    }

    fun trainingFactor(
        cardType: StatusType,
        trainingType: StatusType,
        trainingLevel: Int,
        totalTrainingLevel: Int,
        relation: Int,
        supportTypeCount: Int,
        fanCount: Int,
        status: Status,
        totalRelation: Int,
        trainingSupportCount: Int,
        speedSkillCount: Int,
    ): Int {
        return if (type == 101 && relation >= value0 && value1 == 8) {
            value2
        } else if (type == 102 && relation >= value0 && trainingType != cardType) {
            value1
        } else if (type == 103 && supportTypeCount >= value0) {
            value1
        } else if (type == 104) {
            min(value1, fanCount / value0)
        } else if (type == 108 && value0 == 8) {
            min(value4, ((status.maxHp - value1) * value2 / 100.0 + value3).toInt())
        } else if (type == 109 && value0 == 8) {
            totalRelation / value1
        } else if (type == 110 && value0 == 8) {
            trainingSupportCount * value1
        } else if (type == 111 && value0 == 8) {
            min(5, trainingLevel) * value1
        } else if (type == 114 && value0 == 8) {
            value2 - max(0, (100 - status.hp) / value1)
        } else if (type == 116 && value1 == 8) {
            val skillCount = when (value0) {
                1 -> speedSkillCount
                // TODO
                else -> 0
            }
            value2 * min(skillCount, value3)
        } else if (type == 117 && value0 == 8) {
            min(value2, totalTrainingLevel)
        } else 0
    }

    fun friendFactor(
        relation: Int,
        friendCount: Int,
        status: Status,
    ): Int {
        return if (type == 101 && relation >= value0 && value1 == 1) {
            value2
        } else if (type == 106 && value1 == 1) {
            min(value0, friendCount) * value2
        } else if (type == 107 && value0 == 1) {
            15 - ((max(30, status.hp) - 30) * 15 / 100.0).toInt()
        } else 0
    }

    fun initialStatus(supportType: List<StatusType>): Status {
        return if (type == 105) {
            supportType.fold(Status()) { total, type ->
                when (type) {
                    StatusType.FRIEND, StatusType.GROUP -> total + Status(2, 2, 2, 2, 2)
                    else -> total.add(type to 10)
                }
            }
        } else Status()
    }

    fun failureRate(
    ): Int {
        return if (type == 112) {
            if (Random.nextInt(100) < value0) 0 else 1
        } else 1
    }

    fun hpCost(
        friendTraining: Boolean,
    ): Int {
        return if (type == 113 && friendTraining && value0 == 28) {
            value1
        } else 0
    }

    val initialRelationAll = if (type == 115) 5 else 0

    fun hasSecondPosition(relation: Int) = type == 118 && relation >= value1
}
